const NativeModule       = TurboModuleRegistry.getEnforcing      ('DeviceInfo');
let constants                       = null;
const NativeDeviceInfo = {
  getConstants()                      {
    if (constants == null) {
      constants = NativeModule.getConstants();
    }
    return constants;
  }
};
export default NativeDeviceInfo;