import Event from '../../src/private/webapis/dom/events/Event';
import {
  getEventHandlerAttribute,
  setEventHandlerAttribute,
} from '../../src/private/webapis/dom/events/EventHandlerAttributes';
import EventTarget from '../../src/private/webapis/dom/events/EventTarget';
import NativeFileReaderModule from './NativeFileReaderModule';
import {toByteArray} from 'base64-js';

                 
              
                
       // DONE

                                         

const EMPTY = 0;
const LOADING = 1;
const DONE = 2;

class FileReader extends EventTarget {
  static EMPTY         = EMPTY;
  static LOADING         = LOADING;
  static DONE         = DONE;

  EMPTY         = EMPTY;
  LOADING         = LOADING;
  DONE         = DONE;

  _readyState            ;
  _error        ;
  _result               ;
  _aborted          = false;

  constructor() {
    super();
    this._reset();
  }

  _reset()       {
    this._readyState = EMPTY;
    this._error = null;
    this._result = null;
  }

  _setReadyState(newState            ) {
    this._readyState = newState;
    this.dispatchEvent(new Event('readystatechange'));
    if (newState === DONE) {
      if (this._aborted) {
        this.dispatchEvent(new Event('abort'));
      } else if (this._error) {
        this.dispatchEvent(new Event('error'));
      } else {
        this.dispatchEvent(new Event('load'));
      }
      this.dispatchEvent(new Event('loadend'));
    }
  }

  readAsArrayBuffer(blob       )       {
    this._aborted = false;

    if (blob == null) {
      throw new TypeError(
        "Failed to execute 'readAsArrayBuffer' on 'FileReader': parameter 1 is not of type 'Blob'",
      );
    }

    NativeFileReaderModule.readAsDataURL(blob.data).then(
      (text        ) => {
        if (this._aborted) {
          return;
        }

        const base64 = text.split(',')[1];
        const typedArray = toByteArray(base64);

        this._result = typedArray.buffer;
        this._setReadyState(DONE);
      },
      error => {
        if (this._aborted) {
          return;
        }
        this._error = error;
        this._setReadyState(DONE);
      },
    );
  }

  readAsDataURL(blob       )       {
    this._aborted = false;

    if (blob == null) {
      throw new TypeError(
        "Failed to execute 'readAsDataURL' on 'FileReader': parameter 1 is not of type 'Blob'",
      );
    }

    NativeFileReaderModule.readAsDataURL(blob.data).then(
      (text        ) => {
        if (this._aborted) {
          return;
        }
        this._result = text;
        this._setReadyState(DONE);
      },
      error => {
        if (this._aborted) {
          return;
        }
        this._error = error;
        this._setReadyState(DONE);
      },
    );
  }

  readAsText(blob       , encoding         = 'UTF-8')       {
    this._aborted = false;

    if (blob == null) {
      throw new TypeError(
        "Failed to execute 'readAsText' on 'FileReader': parameter 1 is not of type 'Blob'",
      );
    }

    NativeFileReaderModule.readAsText(blob.data, encoding).then(
      (text        ) => {
        if (this._aborted) {
          return;
        }
        this._result = text;
        this._setReadyState(DONE);
      },
      error => {
        if (this._aborted) {
          return;
        }
        this._error = error;
        this._setReadyState(DONE);
      },
    );
  }

  abort() {
    this._aborted = true;
    // only call onreadystatechange if there is something to abort, as per spec
    if (this._readyState !== EMPTY && this._readyState !== DONE) {
      this._reset();
      this._setReadyState(DONE);
    }
    // Reset again after, in case modified in handler
    this._reset();
  }

  get readyState()             {
    return this._readyState;
  }

  get error()         {
    return this._error;
  }

  get result()                {
    return this._result;
  }

  get onabort()                       {
    return getEventHandlerAttribute(this, 'abort');
  }

  set onabort(listener                ) {
    setEventHandlerAttribute(this, 'abort', listener);
  }

  get onerror()                       {
    return getEventHandlerAttribute(this, 'error');
  }

  set onerror(listener                ) {
    setEventHandlerAttribute(this, 'error', listener);
  }

  get onload()                       {
    return getEventHandlerAttribute(this, 'load');
  }

  set onload(listener                ) {
    setEventHandlerAttribute(this, 'load', listener);
  }

  get onloadstart()                       {
    return getEventHandlerAttribute(this, 'loadstart');
  }

  set onloadstart(listener                ) {
    setEventHandlerAttribute(this, 'loadstart', listener);
  }

  get onloadend()                       {
    return getEventHandlerAttribute(this, 'loadend');
  }

  set onloadend(listener                ) {
    setEventHandlerAttribute(this, 'loadend', listener);
  }

  get onprogress()                       {
    return getEventHandlerAttribute(this, 'progress');
  }

  set onprogress(listener                ) {
    setEventHandlerAttribute(this, 'progress', listener);
  }
}

export default FileReader;
